<h1 id="interface-treequestionoptions-t" tabindex="-1"><a class="header-anchor" href="#interface-treequestionoptions-t" aria-hidden="true">#</a> Interface: TreeQuestionOptions&lt;T&gt;</h1>
<p><strong><code>link</code></strong> https://github.com/insightfuls/inquirer-tree-prompt#readme
Takes type, name, message, tree, [filter, validate, transformer, pageSize, loop, onlyShowValid, hideChildrenOfValid, multiple] properties.</p>
<p>The extra options that this plugin provides are:</p>
<p>tree: (Array) list of tree items or (optionally asynchronous) function to obtain them; items are strings or objects with:</p>
<p>name: (String) to display in list; must provide this or value
value: (String) to put in answers hash; must provide this or name
short: (String) to display after selection
open: (Boolean) whether the item is expanded or collapsed
children: (Array or Function) list of child tree items or (optionally asynchronous) function to obtain them; function may return replacement item instead of just list of children
onlyShowValid: (Boolean) if true, will only show valid items (if validate is provided). Default: false.</p>
<p>hideChildrenOfValid: (Boolean) if true, will hide children of valid items (if validate is provided). Default: false.</p>
<p>transformer: (Function) a hook function to transform the display of item's value (when name is not given).</p>
<p>multiple: (Boolean) if true, will enable to select multiple items. Default: false.</p>
